import { LabelForm } from "../components/labels/LabelForm";
import { InputText } from '../components/input/InputText';
import { mask } from "../services/maskMoney";
import { useState } from "react";
import { onCreateSales } from "../reducers/salesSlice";
import { useDispatch, useSelector } from 'react-redux';
import { CircularIndeterminate } from '../components/LoaderCircular';
import { TIMEOUT, HTTP_RESPONSE } from '../utils/constants';
import { salesService } from "../services/salesService";
import { ReducerStore } from '../app/store';
import { AlertInfo } from "../components/alerts/AlertInfo";
import { AlertSuccess } from '../components/alerts/AlertSuccess';
import { AlertError } from '../components/alerts/AlertError';

type InputProps = {
    id: string;
    data_target: string;
}

export function CreateSalesBySchedule(props: InputProps) {
    const { id, data_target } = props;

    const auth = useSelector((state: ReducerStore) => state.authenticated);
    const salesCreate = useSelector((state: ReducerStore) => state.sales)
    const dispatch = useDispatch();

    const [price, setPrice] = useState<string>('');
    const [isLoadingCreateSales, setIsLoadingCreateSales] = useState<boolean>(false);
    const [alert, setAlert] = useState<JSX.Element | null>(null);

    const saveSale = async (event: React.BaseSyntheticEvent) => {
        event.preventDefault();

        if (!price || price.length === 0 || price.length > 15) {
            setAlert(<AlertInfo title="Preencha o preo corretamente!" />);
            return;
        };

        setIsLoadingCreateSales(true);

        const request = await salesService.newSale(
            auth.token,
            salesCreate.procedure,
            salesCreate.client,
            price,
            salesCreate.date,
        );

        setIsLoadingCreateSales(false);

        if (HTTP_RESPONSE.SUCCESS.includes(request.status)) {
            setAlert(<AlertSuccess title="Venda gerada com sucesso!" />);
            dispatch(
                onCreateSales({
                    id: '',
                    client: '',
                    procedure: '',
                    date: '',
                    price: '',
                })
            )
            setPrice('');
            return;
        }

        if (request.status === HTTP_RESPONSE.ERROR || !request.status) {
            setAlert(<AlertError title="Erro ao precessar a requisio!" />);
        }
    };

    let loader = null;
    if (isLoadingCreateSales) {
        loader = <CircularIndeterminate />;
    } else {
        loader = null;
    }

    if (alert) {
        setTimeout(() => setAlert(null), TIMEOUT.FIVE_SECCONDS);
    }

    return (
        <div
            className="modal fade"
            id={id}
            tabIndex={-1}
            role="dialog"
            aria-labelledby="exampleModalLabel"
            aria-hidden="true"
            data-toggle="modal"
        >
            {loader}
            <div className="modal-dialog modal-dialog-centered" role="document">
                <div className="modal-content">
                    <div className="modal-header">
                        <h5 className="modal-title" id="exampleModalLabel">
                            Gerar venda
                        </h5>
                        <button
                            type="button"
                            className="close"
                            data-dismiss="modal"
                            aria-label="Fechar"
                        >
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div className="modal-body mb-2 mt-2">
                        <LabelForm text="Digite o preo" />
                        <InputText
                            type="text"
                            format="currency"
                            className="mb-3"
                            value={price}
                            onChange={(ev: React.BaseSyntheticEvent) => {
                                let val = ev.target.value;
                                const { maskedValue } = mask(val, 2, ',', '.', false, 'R$');
                                setPrice(maskedValue);
                            }}
                            id="price"
                            label="Digite o preo"
                        />
                    </div>
                    {alert && (
                        <div className="ml-3 mr-3">
                            {alert}
                        </div>
                    )}
                    <div className="modal-footer">
                        <button
                            type="button"
                            className="btn btn-secondary mb-2"
                            data-dismiss="modal"
                        >
                            Fechar
                        </button>
                        <button
                            type="button"
                            onClick={(e) => saveSale(e)}
                            data-target={data_target}
                            className='btn btn-primary mb-2'
                        >
                            Gerar
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
}